<template>
  <div id="app">
    <nav id="nav">
      <div class="container">
        <router-link class="logo" to="/">Akai Zhao 趙生凱</router-link>
        <div class="toggle" @click="NavgationOn"></div>
      </div>
    </nav>
    <div class="container">
      <router-view/>
    </div>
    <NavList :isNavgationOpen="isNavgationOpen" @isNavgationOpenChanged="onIsNavgationOpenChanged"/>
  </div>
</template>
<script>
import NavList from "@/components/NavList.vue";
export default {
  data() {
    return {
      isNavgationOpen: false
    };
  },
  components: {
    NavList
  },
  methods: {
    NavgationOn() {
      this.isNavgationOpen = !this.isNavgationOpen;
    },
    onIsNavgationOpenChanged(val) {
      this.isNavgationOpen = val;
    }
  },
  watch: {
    $route() {
      this.isNavgationOpen = false;
    },
  }
};
</script>

